import axios from '@axios'

export default {
  namespaced: true,
  state: {},
  getters: {},
  mutations: {},
  actions: {
    fetchCitas(ctx, { queryParams }) {
      return new Promise((resolve, reject) => {
        axios
          .get('/citas', { params: queryParams })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    fetchCita(ctx, { id }) {
      return new Promise((resolve, reject) => {
        axios
          .get(`/citas/${id}/edit`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    addCita(ctx, citaData) {
      console.log(citaData)
      return new Promise((resolve, reject) => {
        axios
          .post('/citas', citaData)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    updateCita(ctx, citaData) {
      console.log(citaData)
      return new Promise((resolve, reject) => {
        axios
          .put(`/citas/${citaData.id}`, citaData)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
    deleteCita(ctx, citaData) {
      return new Promise((resolve, reject) => {
        axios
          .delete(`/citas/${citaData.id}`)
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
  },
}
