<template>
  <div>
    <buscar-persona
      @set-add-cita="(val) => persona = val"
    />

    <b-card
      no-body
      class="mb-0"
    >
      <div class="m-2">
        <!-- <pre>{{ persona }}</pre> -->
        <!-- Table Top -->
        <b-row>

          <!-- Per Page -->
          <b-col
            cols="12"
            md="6"
            class="d-flex align-items-center justify-content-start mb-1 mb-md-0"
          >
            <label>Mostrar</label>
            <v-select
              v-model="perPage"
              :options="pageOptions"
              :clearable="false"
              class="per-page-selector d-inline-block mx-50"
            />
            <label>registros</label>
          </b-col>

          <!-- Search -->
          <b-col
            cols="12"
            md="6"
          >
            <div class="d-flex align-items-center justify-content-end">
              <b-form-input
                v-model="filter"
                type="search"
                class="d-inline-block mr-1"
                placeholder="Buscar..."
                debounce="500"
              />
              <b-button
                v-ripple.400="'rgba(113, 102, 240, 0.15)'"
                v-b-modal.modal-add-cita
                variant="primary"
              >
                <span class="text-nowrap ">Agregar cita</span>
              </b-button>
            </div>
          </b-col>
        </b-row>

      </div>

      <!-- Main table element -->
      <b-table
        id="my-table"
        ref="refTablaUsuarios"
        class="position-relative"
        show-empty
        head-variant="light"
        hover
        small
        responsive
        :busy.sync="isBusy"
        :items="myProvider"
        :fields="fields"
        :current-page="currentPage"
        :empty-text="'No hay registros para mostrar'"
        :empty-filtered-text="'No hay registros que coincidan con su solicitud.'"
        :per-page="perPage"
        :filter="filter"
        :filter-included-fields="filterOn"
        :sort-by="sortBy"
        :sort-desc="sortDesc"
      >
        <template #table-busy>
          <div class="text-center text-danger my-2">
            <b-spinner class="align-middle" />
            <strong>Cargando...</strong>
          </div>
        </template>
        <!-- Column: User -->
        <template #cell(usuario)="data">
          <b-media vertical-align="center">
            <template #aside>
              <b-avatar
                size="32"
                :variant="`light-${resolveUserServicioVariant(data.item.usuario.perfil.persona.nombres)}`"
                :text="avatarText(data.item.usuario.perfil.persona.nombres)"
              />
            </template>
            <b-link
              class="font-weight-bold d-block text-nowrap"
            >
              {{ data.item.usuario.perfil.persona.nombre_completo }}
            </b-link>
            <small class="text-muted">N° {{ data.item.usuario.perfil.persona.numero_documento }}</small> <br>
            <small class="text-muted">CEL: {{ data.item.usuario.perfil.persona.celular }}</small>
          </b-media>
        </template>

        <!-- Servicios -->
        <template #cell(servicios)="data">
          <div
            v-for="(servicio, index) in data.item.servicios"
            :key="index"
          >
            <b-badge
              pill
              :variant="`light-${resolveColorServicioVariant(servicio)}`"
              class="text-capitalize"
            >
              {{ servicio.nombre }}
            </b-badge>
          </div>

        </template>

        <!-- Column: Status -->
        <template #cell(estado)="data">
          <b-badge

            :variant="`light-${resolveCitaStatusVariant(data.item.estado)}`"
            class="text-capitalize"
          >
            {{ data.item.estado }}
          </b-badge>
        </template>

        <!-- Column: Actions -->
        <template #cell(acciones)="data">
          <b-dropdown
            variant="link"
            no-caret
            :right="$store.state.appConfig.isRTL"
          >

            <template #button-content>
              <feather-icon
                icon="MoreVerticalIcon"
                size="16"
                class="align-middle text-body"
              />
            </template>
            <b-dropdown-item>
              <feather-icon icon="EditIcon" />
              <span class="align-middle ml-50">Editar</span>
            </b-dropdown-item>
            <b-dropdown-item
              @click="validarCita(data.item)"
            >
              <feather-icon icon="CheckSquareIcon" />
              <span class="align-middle ml-50">Validar</span>
            </b-dropdown-item>

            <b-dropdown-item
              @click="deleteItem(data.item)"
            >
              <feather-icon icon="TrashIcon" />
              <span class="align-middle ml-50">Eliminar</span>
            </b-dropdown-item>
          </b-dropdown>
        </template>

      </b-table>

      <div class="mx-2 mb-2">
        <b-row>
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-start"
          >
            <span class="text-muted">Mostrando registros del {{ from }} al {{ to }} de un total de {{ totalRows }} registros</span>

          </b-col>
          <!-- Pagination -->
          <b-col
            cols="12"
            sm="6"
            class="d-flex align-items-center justify-content-center justify-content-sm-end"
          >

            <b-pagination
              v-model="currentPage"
              :total-rows="totalRows"
              :per-page="perPage"
              first-number
              last-number
              class="mb-0 mt-1 mt-sm-0"
              prev-class="prev-item"
              next-class="next-item"
            >
              <template #prev-text>
                <feather-icon
                  icon="ChevronLeftIcon"
                  size="18"
                />
              </template>
              <template #next-text>
                <feather-icon
                  icon="ChevronRightIcon"
                  size="18"
                />
              </template>
            </b-pagination>

          </b-col>
        </b-row>
      </div>

    </b-card>

    <citas-add-form-modal
      :persona="persona"
      :edited-item="editedItem"
      @refresh-data-citas-table="actualizarTabla"
    />
  </div>
</template>

<script>
import {
  BCard, BTable, BSpinner, VBModal, BRow, BCol, BFormInput, BDropdown, BDropdownItem, BPagination, BBadge, BButton,
  BMedia,
  BAvatar,
  BLink,
} from 'bootstrap-vue'

import vSelect from 'vue-select'
import { onUnmounted } from '@vue/composition-api'
import store from '@/store'
import Ripple from 'vue-ripple-directive'
import { avatarText } from '@core/utils/filter'
// Notification
// import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import CitasAddFormModal from '@/views/salud/cita/citas-create/CitasAddFormModal.vue'
import BuscarPersona from '@/views/salud/cita/BuscarPersona.vue'
import citasStoreModule from '../citasStoreModule'
import useSaludUtilitarios from '../../useSaludUtilitarios'

export default {
  components: {
    BTable,
    BBadge,
    vSelect,
    BSpinner,
    BCard,
    BRow,
    BCol,
    BFormInput,
    BButton,
    BDropdown,
    BDropdownItem,
    BPagination,
    BMedia,
    BAvatar,
    BLink,
    // componentes local
    CitasAddFormModal,
    BuscarPersona,
  },
  directives: {
    'b-modal': VBModal,
    Ripple,
  },
  data() {
    return {
      persona: {},
      editedItem: {
      },
      searchQuery: null,
      isBusy: false,
      items: [],
      from: 1,
      to: 1,
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, 50],
      sortBy: 'id',
      sortDesc: false,
      sortDirection: 'desc',
      filter: '',
      filterOn: [],
    }
  },
  methods: {
    validarCita(item) {
      console.log(item)
      const { usuario, ...rest } = item
      const { perfil } = usuario
      const { persona } = perfil
      this.editedItem = { ...rest, persona }
      this.$root.$emit('bv::show::modal', 'modal-agregar-cita', '#refModalAgregarCita')
    },
    deleteItem(item) {
      // Implemente swal
      this.$swal({
        title: '¿Estas seguro?',
        text: '¡No podrás revertir esto!',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Si, bórralo!',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          this.$http.delete(`citas/${item.id}`)
          // store.dispatch('citas/deleteCita', item)
            .then(() => {
              this.$swal({
                icon: 'success',
                title: 'Eliminado!',
                text: 'Tu cita ha sido eliminado.',
                customClass: {
                  confirmButton: 'btn btn-success',
                },
              })
              this.actualizarTabla()
            }).catch(error => {
              this.$swal({
                title: 'Error',
                text: `${error}`,
                icon: 'error',
                customClass: {
                  confirmButton: 'btn btn-success',
                },
              })
            })
        } else if (result.dismiss === 'cancel') {
          this.$swal({
            title: 'Cancelado',
            text: 'Tu cita está seguro :)',
            icon: 'error',
            customClass: {
              confirmButton: 'btn btn-success',
            },
          })
        }
      })
    },
    actualizarTabla() {
      this.$refs.refTablaUsuarios.refresh()
    },
    async myProvider(ctx) {
      const promise = store.dispatch('citas/fetchCitas', { queryParams: ctx })

      // Must return a promise that resolves to an array of items
      return promise.then(response => {
        const {
          data, total, from, to,
        } = response.data
        // Pluck the array of items off our axios response
        const items = data
        console.log(items)

        this.totalRows = total || 0
        // this.from = meta.pagination.count
        this.from = from || 0

        this.to = to || 0
        // Must return an array of items or an empty array if an error occurred
        return items || []
      })
    },
  },
  setup() {
    const CITAS_APP_STORE_MODULE_NAME = 'citas'

    // Register module
    if (!store.hasModule(CITAS_APP_STORE_MODULE_NAME)) store.registerModule(CITAS_APP_STORE_MODULE_NAME, citasStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(CITAS_APP_STORE_MODULE_NAME)) store.unregisterModule(CITAS_APP_STORE_MODULE_NAME)
    })

    const resolveCitaStatusVariant = val => {
      if (val === 'aprobado') return 'success'
      if (val === 'no aprobado') return 'warning'
      if (val === 'cancelado') return 'danger'
      return 'primary'
    }

    const fields = [
      { key: 'usuario', label: 'paciente', sortable: true },
      {
        key: 'servicios',
        label: 'servicios',
        sortable: true,
        // formatter: 'formatServicio',
      },
      { key: 'fecha_cita', label: 'f. cita', sortable: true },
      { key: 'created_at', label: 'f. registro', sortable: true },
      { key: 'estado', sortable: true },
      { key: 'acciones' },
    ]

    const resolveUserServicioVariant = val => {
      if (val === 'FARMACIA') return 'primary'
      if (val === 'ODONTOLOGÍA') return 'warning'
      if (val === 'OFTAMOLOGÍA') return 'success'
      if (val === 'TÓPICO') return 'info'
      if (val === 'GINECO-OBSTÉTRICO') return 'danger'
      return 'primary'
    }

    const { resolveColorServicioVariant } = useSaludUtilitarios()

    return {
      // Filter
      avatarText,
      fields,
      resolveCitaStatusVariant,
      resolveUserServicioVariant,
      resolveColorServicioVariant,
    }
  },
}
</script>
<style lang="scss" scoped>
.per-page-selector {
  width: 90px;
}
</style>

<style lang="scss">
@import '@core/scss/vue/libs/vue-select.scss';
</style>
