<template>
  <!-- modal login-->
  <b-modal
    id="modal-agregar-cita"
    ref="refModalAgregarCita"
    v-model="dialog"
    centered
    :title="formTitle"
    :hide-footer="true"
    size="lg"
    no-close-on-backdrop
    no-close-on-esc
    hide-header-close
  >
    <b-form @submit.prevent="onSubmit">
      <b-row class="d-flex justify-content-center">
        <h4 class="text-uppercase font-weight-bold text-primary">
          {{ citaData.persona.nombre_completo }}
        </h4>
      </b-row>
      <b-row class="d-flex justify-content-center">
        <strong><i>DNI: </i></strong>
        <p class="font-weight-bold font-italic font-weight-bold">
          {{ citaData.persona.numero_documento }}
        </p>
      </b-row>
      <b-row>
        <b-col cols="12">
          <!-- Input servicio -->
          <b-form-group
            label="Servicio"
          >
            <v-select
              v-model="citaData.servicios"
              :dir="$store.state.appConfig.isRTL ? 'rtl' : 'ltr'"
              label="nombre"
              :options="optionsTipos"
              :reduce="val => val.id"
              multiple
              placeholder="Seleccionar"
            />
          </b-form-group>
        </b-col>
      </b-row>
      <b-row>
        <b-col md="6">
          <b-form-group label="Celular para contacto">
            <b-form-input
              v-model="citaData.celular"
              type="text"
              required
            />
          </b-form-group>
        </b-col>

        <b-col md="6">
          <b-form-group label="Fecha de la cita">
            <flat-pickr
              v-model="citaData.fecha_cita"
              :config="config"
              class="form-control"
            />
          </b-form-group>
        </b-col>
      </b-row>

      <b-form-group label="Motivo:">
        <b-form-textarea
          v-model="citaData.motivo"
          placeholder="Describir motivo..."
          rows="3"
          required
          max-rows="6"
        />
      </b-form-group>

      <b-row class="mt-1">
        <b-col>
          <b-button
            variant="success"
            class="mb-1 mb-sm-0 mr-0 mr-sm-1"
            :block="$store.getters['app/currentBreakPoint'] === 'xs'"
            type="submit"
          >
            Guardar
          </b-button>
          <b-button
            variant="outline-secondary"
            class="mb-1 mb-sm-0 mr-0 mr-sm-1"
            :block="$store.getters['app/currentBreakPoint'] === 'xs'"
            @click="close"
          >
            Cerrar
          </b-button>
          <b-button
            variant="danger"
            :block="$store.getters['app/currentBreakPoint'] === 'xs'"
            @click="aprobarCita"
          >
            Aprobar
          </b-button>
        </b-col>
      </b-row>
    </b-form>
  </b-modal>

</template>

<script>
import {
  BRow, BCol, BForm, BModal, BFormGroup, BFormInput, BFormTextarea, BButton,
} from 'bootstrap-vue'
import flatPickr from 'vue-flatpickr-component'
// eslint-disable-next-line import/extensions
// eslint-disable-next-line import/no-extraneous-dependencies
import { Spanish } from 'flatpickr/dist/l10n/es'
import vSelect from 'vue-select'
import Ripple from 'vue-ripple-directive'
import {
  ref, watch, toRefs, nextTick, computed,
} from '@vue/composition-api'
import store from '@/store'
// Notification
import { useToast } from 'vue-toastification/composition'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import axios from '@axios'

export default {
  components: {
    BRow,
    BCol,
    BModal,
    BForm,
    BFormGroup,
    BFormInput,
    BFormTextarea,
    BButton,

    // Externos
    flatPickr,
    vSelect,
  },
  directives: {
    Ripple,
  },
  props: {
    persona: {
      type: Object,
      default() {
        return { }
      },
    },
    editedItem: {
      type: Object,
      default() {
        return { }
      },
    },
  },
  data() {
    return {
      // Spanish,
      config: {
        static: true,
        // inline: true,
        // wrap: true, // set wrap to true only when using 'input-group'
        // allowInput: true,
        enableTime: true,
        // dateFormat: 'F j, Y h:i K',
        dateFormat: 'Y-m-d H:i',
        altFormat: 'd/m/Y h:i K',
        altInput: true,
        locale: Spanish, // locale for this instance only
        // time_24hr: true,
      },
      selected: null,
      selectedEstado: null,
      optionsTipos: [],
      optionsEstado: [{ id: 1, nombre: 'aprobado' }, { id: 2, nombre: 'no aprobado' }, { id: 3, nombre: 'cancelado' }],
    }
  },
  created() {
    this.$http.get('servicios/tipos').then(response => {
      this.optionsTipos = response.data.data
    })
  },
  setup(props, { emit }) {
    const { persona } = toRefs(props)
    const { editedItem } = toRefs(props)

    const editedIndex = ref(-1)
    const dialog = ref(false)
    const toast = useToast()

    const blankCitaData = {
      id: null,
      paciente_id: null,
      servidor_publico: null,
      motivo: null,
      celular: null,
      estado: 'no aprobado',
      fecha_cita: null,
      servicios: [],
      persona: {
        nombre_completo: null,
        numero_documento: null,
      },
    }

    const citaData = ref(JSON.parse(JSON.stringify(blankCitaData)))

    const resetuserData = () => {
      citaData.value = JSON.parse(JSON.stringify(blankCitaData))
    }

    const close = () => {
      dialog.value = false
      nextTick(() => {
        resetuserData()
        editedIndex.value = -1
      })
    }

    const formTitle = computed(() => (editedIndex.value === -1 ? 'Registrar nueva cita' : `Editar cita N° ${editedIndex.value}`))

    watch(dialog, () => {
      // eslint-disable-next-line no-unused-expressions
      dialog.value || close()
    })

    watch(persona, () => {
      citaData.value.paciente_id = persona.value.usuario_id
      citaData.value.persona = persona.value
      editedIndex.value = -1
      dialog.value = true
    })

    watch(editedItem, () => {
      editedIndex.value = editedItem.value.id
      citaData.value = editedItem.value
      citaData.value.servicios = editedItem.value.servicios.length > 0 ? editedItem.value.servicios.map(item => item.id) : []
      // citaData.value.celular = editedItem.value.celular
      // citaData.value.fecha_cita = editedItem.value.fecha_cita
      // citaData.value.motivo = editedItem.value.motivo
      // citaData.value.servicios = editedItem.value.servicios
      dialog.value = true
    })

    const aprobarCita = () => {
      editedItem.value.estado = 'aprobado'
      editedItem.value.cita_id = editedItem.value.id
      editedItem.value.fecha_atencion = editedItem.value.fecha_cita
      axios.put(`citas/${editedItem.value.id}/crear-triaje`, editedItem.value).then(response => {
        console.log(response)
        dialog.value = false
        toast({
          component: ToastificationContent,
          props: {
            title: 'Aprobación exitosa',
            icon: 'AlertTriangleIcon',
            variant: 'success',
          },
        })
        emit('refresh-data-citas-table')
      }).catch(error => {
        toast({
          component: ToastificationContent,
          props: {
            title: 'Error!',
            icon: 'EditIcon',
            text: `${error.response.data.error}`,
            variant: 'danger',
          },
        })
      })
    }

    const onSubmit = () => {
      if (editedIndex.value > -1) {
      // vamos a editar
        console.log('Vamos a editar')
        store.dispatch('citas/updateCita', citaData.value)
          .then(() => {
          // Clean form, Close Modal y Refresh Table
            toast({
              component: ToastificationContent,
              props: {
                title: '¡Actualización exitosa',
                icon: 'AlertTriangleIcon',
                variant: 'success',
              },
            })
            emit('refresh-data-citas-table')
          })
          .catch(error => {
            console.log(error)
          })
      } else {
        console.log('Vamos a un nuevo registro')
        store.dispatch('citas/addCita', citaData.value)
          .then(() => {
          // Clean form, Close Modal y Refresh Table
            toast({
              component: ToastificationContent,
              props: {
                title: '¡Registro exitoso!',
                icon: 'AlertTriangleIcon',
                variant: 'success',
              },
            })
            dialog.value = false
            emit('refresh-data-citas-table')
          })
      }
    }

    return {
      citaData,
      resetuserData,
      onSubmit,
      close,
      dialog,
      formTitle,
      aprobarCita,
    }
  },

}
</script>

<style lang="scss">
@import '@core/scss/vue/libs/vue-flatpicker.scss';
@import '@core/scss/vue/libs/vue-select.scss';
</style>
